import { Component, OnInit, NgZone, ViewChild } from '@angular/core';
import { UserService } from 'src/app/contactmanager/service/user.service';
import { Observable } from 'rxjs';
import { User } from 'src/app/contactmanager/models/user';
import { HttpClient , HttpClientModule } from '@angular/common/http';
import { Router } from '@angular/router';
import { MatSidenav } from '@angular/material/sidenav';

const SMALL_WIDTH_BREAKPOINT = 720;
@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css']
})
export class SidenavComponent implements OnInit {
  private mediaMatcher: MediaQueryList = matchMedia(`(max-width : ${SMALL_WIDTH_BREAKPOINT}px)`);
  users: Observable<User[]>;


  constructor(zone: NgZone , private router: Router , private userService: UserService, http: HttpClient, httpm: HttpClientModule) {
        // this.mediaMatcher.addListener(mql =>
         // zone.run(()=> this.mediaMatcher =mql)
          // );
   }

   @ViewChild (MatSidenav, {static: false}) sidenav: MatSidenav;

  ngOnInit() {
    this.users = this.userService.users;
    this.userService.loadAll();

    this.router.events.subscribe(() => {
      if (this.isSmallSceeen()) {
        this.sidenav.close();
      }
    });
  }
  isSmallSceeen(): boolean {   return this.mediaMatcher.matches;
  }
}
