import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ContactmanagerAppComponent } from './contactmanager-app/contactmanager-app.component';
import { ToolbarComponent } from './components/toolbar/toolbar.component';
import { SidenavComponent } from './components/toolbar/sidenav/sidenav.component';
import { MainContentComponent } from './components/toolbar/main-content/main-content.component';


import { FlexLayoutModule } from '@angular/flex-layout';
import {MaterialModule} from '../shared/material/material.module';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { UserService } from './service/user.service';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { NotesComponent } from './components/notes/notes.component';
import { NewContactDialogComponent } from './components/new-contact-dialog/new-contact-dialog.component';

const routes: Routes =
[
  {path: '', component : ContactmanagerAppComponent ,
    children :
    [
      {path : '', component : MainContentComponent},
      {path : ':id', component : MainContentComponent}
    ]},
  {path: '**' , redirectTo: ''}
];


@NgModule({
  // tslint:disable-next-line: max-line-length
  declarations: [ContactmanagerAppComponent, ToolbarComponent, SidenavComponent, MainContentComponent, NotesComponent, NewContactDialogComponent],
  imports: [
    CommonModule,
    HttpClientModule,
    FormsModule,
    FlexLayoutModule,
    MaterialModule,
    RouterModule.forChild(routes)
  ],
  providers:
  [UserService],
  entryComponents: [NewContactDialogComponent]
})
export class ContactmanagerModule { }
